  .note-placeholder {
	position: absolute;
	display: none;
	color: grey;
  }
  
  .note-handle .note-control-selection {
	position: absolute;
	display: none;
	border: 1px solid #{$color};
  
	> div {
	  position: absolute;
	}
  
	.note-control-selection-bg {
	  width: 100%;
	  height: 100%;
	  background-color: #{$color};
	  -webkit-opacity: .3;
	  -khtml-opacity: .3;
	  -moz-opacity: .3;
	  opacity: .3;
	  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=30);
	  filter: alpha(opacity = 30);
	}
  
	.note-control-handle, .note-control-holder {
	  width: 7px;
	  height: 7px;
	  border: 1px solid #{$color};
	}
  
	.note-control-sizing {
	  width: 7px;
	  height: 7px;
	  border: 1px solid #{$color};
	  background-color: #{$color};
	}
  
	.note-control-nw {
	  inset-block-start: -5px;
	  inset-inline-start: -5px;
	  border-inline-end: none;
	  border-block-end: none;
	}
  
	.note-control-ne {
	  inset-block-start: -5px;
	  inset-inline-end: -5px;
	  border-block-end: none;
	  border-inline-start: none;
	}
  
	.note-control-sw {
	  inset-block-end: -5px;
	  inset-inline-start: -5px;
	  border-block-start: none;
	  border-inline-end: none;
	}
  
	.note-control-se {
	  inset-inline-end: -5px;
	  inset-block-end: -5px;
	  cursor: se-resize;
  
	  &.note-control-holder {
		cursor: default;
		border-block-start: none;
		border-inline-start: none;
	  }
	}
  
	.note-control-selection-info {
	  inset-inline-end: 0;
	  inset-block-end: 0;
	  padding: 5px;
	  margin: 5px;
	  color: rgb($white);
	  background-color: #{$color};
	  font-size: 12px;
	  border-radius: 5px;
	  -webkit-opacity: .7;
	  -khtml-opacity: .7;
	  -moz-opacity: .7;
	  opacity: .7;
	  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=70);
	  filter: alpha(opacity = 70);
	}
