  .note-hint-popover {
	min-width: 100px;
	padding: 2px;
  
	.popover-content {
	  padding: 3px;
	  max-height: 150px;
	  overflow: auto;
  
	  .note-hint-group .note-hint-item {
		display: block !important;
		padding: 3px;
  
		&.active, &:hover {
		  display: block;
		  clear: both;
		  font-weight: 400;
		  line-height: 1.4;
		  color: rgb($white);
		  white-space: nowrap;
		  text-decoration: none;
		  background-color: #428bca;
		  outline: 0;
		  cursor: pointer;
		}
	  }
	}
  }
  
  .note-btn {
	&.dropdown-toggle::after {
	  display: none;
